<template>
  <div
    id="scroll"
    v-if="currentInfo"
  >
    <div
      class="flex"
      style="margin-top:10px;font-weight:bold;align-items:baseline;"
      @click="isInCome = !isInCome"
    >
      <div
        class="w30"
        style="color:#000;"
      >房费结算金额</div>
      <div class="w70">
        <div style="color:#57D092;display:flex;justify-content:flex-end;align-items:center;">
          <div>￥{{ currentInfo.balanceAmount | filterP }}</div>
          <van-icon :name="isInCome == false?'arrow':'arrow-down'" />
        </div>
        <div style="font-weight:normal;font-size:14px;color:#898989;">顾客支付价&emsp;￥{{ currentInfo.roomAmount | filterP }}</div>
      </div>
    </div>
    <div v-if="isInCome">
      <div class="flex">
        <div class="w30">房费收入</div>
        <div class="w70">￥{{ currentInfo.balanceRoomAmount | filterP }}</div>
      </div>
      <div
        class="flex"
        v-if="$route.query.selected == '1'"
      >
        <div class="w30">延时退房</div>
        <div class="w70">￥{{ currentInfo.autoDepositCutPay | filterP }}</div>
      </div>
    </div>
    <div
      class="flex"
      style="margin-top:10px;color:#000;font-weight:bold;"
      @click="isOutCome = !isOutCome"
    >
      <div
        class="w30"
        style="color:#000;"
      >运营支出</div>
      <div
        class="w70"
        style="color:#FF5257;"
      >-￥{{ outComeTotel | filterP }}</div>
      <van-icon :name="isOutCome == false?'arrow':'arrow-down'" />
    </div>
    <div v-if="isOutCome">
      <div class="flex">
        <div class="w60">运营服务费<span style="font-size:12px;padding-left:10px;">顾客支付价*{{ currentInfo.maCommission }}%</span></div>
        <div class="w40">-￥{{ currentInfo.maAmount | filterP }}</div>
      </div>
      <div
        class="flex"
        @click="goNext_day"
      >
        <div class="w30">保洁人工费</div>
        <div class="w70 icon_flex">-￥{{ currentInfo.cAmount | filterP }}
          <van-icon
            v-if="$route.query.selected == '1'"
            color="#b2b2b2"
            name="arrow"
          />
        </div>
      </div>
      <div
        class="flex"
        @click="goNext({name:'日常消耗品'})"
      >
        <div class="w30">日常消耗品</div>
        <div class="w70 icon_flex">-￥{{ currentInfo.cleanConAmount | filterP }}
          <van-icon
            color="#b2b2b2"
            name="arrow"
          />
        </div>
      </div>
      <div
        class="flex"
        @click="goNext({name:'日常洗涤费用'})"
      >
        <div class="w30">日常洗涤费用</div>
        <div class="w70 icon_flex">-￥{{ currentInfo.cleanWashAmount | filterP }}
          <van-icon
            color="#b2b2b2"
            name="arrow"
          />
        </div>
      </div>
    </div>
    <div
      v-if="$route.query.selected == '1' && $route.query.billTime.length == '7'"
      class="flex"
      style="margin-top:10px;color:#000;font-weight:bold;"
      @click="isOutOther = !isOutOther"
    >
      <div
        class="w30"
        style="color:#000;"
      >其他支出</div>
      <div
        class="w70"
        style="color:#FF5257;"
      >-￥{{ outOtherTotle | filterP }}</div>
      <van-icon
        v-if="otherOutAll.length > 0"
        :name="isOutOther == false?'arrow':'arrow-down'"
      />
    </div>
    <div v-if="isOutOther">
      <div
        class="flex"
        v-for="(item,i) in otherOutAll"
        :key="i"
        @click="showAdd(item,'1')"
      >
        <div
          class="w30"
          style="width:70%"
        >
          <div style="color:#000;">{{ item.costName }}</div>
          <div style="font-size:14px">{{ item.costDate }}</div>
        </div>
        <div
          class="w70 icon_flex"
          style="width:30%"
        >-￥{{ item.costAmount | filterP }}
          <van-icon
            v-if="is_roleManager == true"
            color="#b2b2b2"
            name="arrow"
          />
        </div>
      </div>
      <div
        v-if="$route.query.billTime.length == '7' && $route.query.selected =='1' && is_roleManager == true"
        class="addOther"
        @click="showAdd('','1')"
      >添加支出项目</div>
    </div>
    <div
      v-if="$route.query.selected == '1' && $route.query.billTime.length == '7'"
      class="flex"
      style="margin-top:10px;color:#000;font-weight:bold;"
      @click="isInOther = !isInOther"
    >
      <div
        class="w30"
        style="color:#000;"
      >其他收入</div>
      <div
        class="w70"
        style="color:#57D092"
      >+￥{{ inOtherTotle | filterP }}</div>
      <van-icon
        v-if="otherInAll.length > 0"
        :name="isInOther == false?'arrow':'arrow-down'"
      />
    </div>
    <div v-if="isInOther">
      <div
        class="flex"
        v-for="(item,i) in otherInAll"
        :key="i"
        @click="showAdd(item,'2')"
      >
        <div
          class="w30"
          style="width:70%"
        >
          <div style="color:#000;">{{ item.costName }}</div>
          <div style="font-size:14px">{{ item.costDate }}</div>
        </div>
        <div
          class="w70 icon_flex"
          style="width:30%"
        >+￥{{ item.costAmount | filterP }}
          <van-icon
            v-if="is_roleManager == true"
            color="#b2b2b2"
            name="arrow"
          />
        </div>
      </div>
      <div
        v-if="$route.query.billTime.length == '7' && $route.query.selected =='1' && is_roleManager == true"
        class="addOther"
        @click="showAdd('','2')"
      >添加收入项目</div>
    </div>
    <div
      class="flex"
      style="margin-top:10px;font-weight:bold;"
    >
      <div
        class="w30"
        style='color:#000;'
      >结算金额</div>
      <div class="w70 f7">{{ symbol }}￥{{ (Math.abs(currentInfo.balanceAmount - outComeTotel - outOtherTotle + inOtherTotle)).toFixed(2) }}</div>
    </div>
    <van-popup
      position='bottom'
      v-model="show_botttom"
    >
      <div
        class="f7 b_f2"
        @click="edit"
      >编辑</div>
      <div
        class="f85 b_f2"
        @click="removeItem"
      >删除此项{{ my_type == '2'?'收入':'支出' }}</div>
      <div
        class="f8 b_f2"
        style="border-top:8px solid #f2f2f2;"
        @click="show_botttom = false"
      >取消</div>
    </van-popup>
    <van-dialog
      :title="my_type == '2'?font+'其他收入':font+'其他支出'"
      v-model="show_addOrEdit"
      :before-close="confirmDepart"
      show-cancel-button
    >
      <div>
        <van-cell-group>
          <van-field
            v-model="dataAdd.costName"
            :label="my_type =='2'?'收入项目':'支出项目'"
            placeholder="请输入项目名称"
          />
          <van-field
            v-model="dataAdd.costAmount"
            label="金额"
            type="number"
            :placeholder="my_type =='2'?'请输入收入金额':'请输入支出金额'"
          />
        </van-cell-group>
      </div>
    </van-dialog>
  </div>
</template>
<script>
import { Toast, Dialog } from 'vant';
import { isRole } from '@/utils/filters';
import { GetBusinessLandlordBillRecordDetail, GetbusinessLandlordBillRecordMonthDetail, GetbusinessRoomCostQuery, PostbusinessRoomCostAdd, PostbusinessRoomCostEdit, PostbusinessRoomCostRemove } from '@/api/api_landlord';
export default {
    data() {
        return {
            currentInfo: {
                autoDepositCutPay: null,
                balanceAmount: null,
                balanceRoomAmount: null,
                cAmount: null,
                cleanConAmount: null,
                cleanWashAmount: null,
                maAmount: null,
                roomAmount: null,
                maCommission: null
            },
            otherOutAll: [],
            otherInAll: [],
            dataAdd: {
                id: null,
                costName: null,
                costType: null,
                costAmount: null
            },
            symbol: null,
            font: null,
            isInCome: true,
            isOutCome: true,
            isOutOther: true,
            isInOther: true,
            show_addOrEdit: false,
            show_botttom: false,
            is_roleManager: false,
            my_type: null,
            outComeTotel: 0,
            outOtherTotle: 0,
            inOtherTotle: 0
        };
    },
    created() {
        this.$store.commit('setHeader', this.$route.query.roomSn + '房间收支详情');
        let is_roleManager = isRole('ROLE_MANAGER');
        let is_roleCooperManager = isRole('ROLE_HOTEL_MANAGER');
        if (is_roleManager == 'true' || is_roleCooperManager == 'true') {
            this.is_roleManager = true;
        }
        this.getCurrent();
    },
    filters: {
        filterP(price) {
            return Number(price).toFixed(2);
        }
    },
    methods: {
        // 订单详情
        getCurrent() {
            let data = {
                roomId: this.$route.query.billId,
                billTime: this.$route.query.billTime
            };
            let _api = this.$route.query.selected == '1' ? GetBusinessLandlordBillRecordDetail : GetbusinessLandlordBillRecordMonthDetail;
            _api(data).done((res) => {
                this.currentInfo = res.data;
                this.outComeTotel = Number(this.currentInfo.maAmount) + Number(this.currentInfo.cAmount) + Number(this.currentInfo.cleanConAmount) + Number(this.currentInfo.cleanWashAmount);
                this.outOtherTotle = this.currentInfo.roomCost || 0;
                this.inOtherTotle = this.currentInfo.roomIncome || 0;
                this.symbol =
                    Number(this.currentInfo.balanceAmount) - Number(this.outComeTotel) - Number(this.outOtherTotle) + Number(this.inOtherTotle) == 0
                        ? ''
                        : Number(this.currentInfo.balanceAmount) + Number(this.currentInfo.roomIncome) > Number(this.currentInfo.roomCost) + Number(this.outComeTotel)
                        ? '+'
                        : '-';
                if (this.$route.query.selected == '1' && this.currentInfo.roomCost > 0) {
                    this.getOutOther('1');
                }
                if (this.$route.query.selected == '1' && this.currentInfo.roomIncome > 0) {
                    this.getInOther('2');
                }
            });
        },
        getOutOther(num) {
            let data = {
                roomId: this.$route.query.billId,
                costDate: this.$route.query.billTime,
                costType: num
            };
            GetbusinessRoomCostQuery(data).done((res) => {
                this.otherOutAll = res.data;
            });
        },
        getInOther(num) {
            let data = {
                roomId: this.$route.query.billId,
                costDate: this.$route.query.billTime,
                costType: num
            };
            GetbusinessRoomCostQuery(data).done((res) => {
                this.otherInAll = res.data;
            });
        },
        showAdd(item, type) {
            this.my_type = type;
            if (this.is_roleManager == false) {
                return false;
            }
            if (this.$route.query.billTime.length > '7') {
                return false;
            }
            if (item) {
                this.item = item;
                this.show_botttom = true;
            } else {
                this.font = '添加';
                this.dataAdd.id = null;
                this.dataAdd.costName = null;
                this.dataAdd.costAmount = null;
                this.dataAdd.roomId = this.$route.query.billId;
                this.dataAdd.costDate = this.$route.query.billTime;
                this.show_addOrEdit = true;
            }
        },
        // 编辑
        edit() {
            this.show_botttom = false;
            this.font = '编辑';
            this.dataAdd.id = this.item.id;
            this.dataAdd.costName = this.item.costName;
            this.dataAdd.costAmount = this.item.costAmount;
            this.show_addOrEdit = true;
        },
        // 删除
        removeItem() {
            this.show_botttom = false;
            Dialog.confirm({
                message: "确定删除'" + this.item.costName + "'?"
            })
                .then(() => {
                    PostbusinessRoomCostRemove({ roomCostId: this.item.id }).done((res) => {
                        Toast(res.msg);
                        this.getCurrent();
                    });
                    this.show_botttom = false;
                })
                .catch(() => {});
        },
        confirmDepart(action, done) {
            if (action == 'confirm') {
                done(false);
                if (!this.dataAdd.costName) {
                    Toast('请输入项目名称');
                    this.dataAdd.costName = '';
                    return;
                }
                if (!this.dataAdd.costAmount) {
                    Toast('请输入支出金额');
                    this.dataAdd.costAmount = '';
                    return;
                }
                let _api = this.dataAdd.id ? PostbusinessRoomCostEdit : PostbusinessRoomCostAdd;
                this.dataAdd.costType = this.my_type;
                _api(this.dataAdd).done((res) => {
                    Toast(res.msg);
                    this.getCurrent();
                });
                this.show_addOrEdit = false;
            } else if (action == 'cancel') {
                done(false);
                this.show_addOrEdit = false;
            }
        },
        // 保洁人工费
        goNext_day() {
            if (this.$route.query.selected == '1') {
                this.$router.push({
                    path: '/owner-finance-daily-life',
                    query: {
                        name: '保洁人工费',
                        roomId: this.$route.query.billId,
                        billTime: this.$route.query.billTime
                    }
                });
            }
        },
        // 日常消耗品、洗涤费用
        goNext(type) {
            this.$router.push({
                path: '/owner-finance-daily-life',
                query: {
                    name: type.name,
                    orderRoomId: this.currentInfo.orderRoomId,
                    selected: this.$route.query.selected,
                    roomId: this.$route.query.billId,
                    billTime: this.$route.query.billTime,
                    hotelCostType: type.name == '日常消耗品' ? '2' : '3'
                }
            });
        }
    }
};
</script>
<style scoped>
#scroll {
    overflow: auto;
    height: 90%;
    margin: 10px 0;
}
.flex {
    display: flex;
    justify-content: space-between;
    background: #fff;
    font-size: 15px;
    color: #000;
    border-bottom: 1px solid #efecf0;
    align-items: center;
    padding: 10px 20px;
}
.icon_flex {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.addOther {
    color: #ff7d41;
    text-align: left;
    font-size: 15px;
    padding: 10px 20px;
    background: #fff;
}
.w30 {
    width: 30%;
    text-align: left;
    color: #8f8d90;
}
.w70 {
    width: 70%;
    text-align: right;
}
.w60 {
    width: 60%;
    text-align: left;
    color: #8f8d90;
}
.w40 {
    width: 40%;
    text-align: right;
}
.f7 {
    color: #000;
}
.f85 {
    color: #f85056;
}
.f8 {
    color: #888;
}
.b_f2 {
    font-size: 15px;
    padding: 10px 0;
    border-top: 1px solid #f2f2f2;
}
/deep/ .van-dialog__header {
    font-weight: bold;
    color: #000;
    padding: 20 0;
}
</style>



